.banner-two {
  padding: 100px 0;
  background: #fff;
  & h2 {
    text-align: center;
    position: relative;
    &::after {
      content: '';
      display: block;
      position: absolute;
      background: url('./../img/arrow-right.png') no-repeat center;
      background-size: contain;
      width: 100px;
      height: 120px;
      right: 150px;
      top: 30px;
      transform: translate(50%, -50%);
    }
  }
  & p {
    margin-left: 275px;
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 600;
    font-size: 24px;
    font-family: 'Playfair Display';
  }
}
