@media (max-width: 575px) {
  .banner-one, .outlook, .autobonus, .stats, .banner-two, .benefit, .ready, .application {
    padding: 25px 0;
  }
  .video-youtube__content {
    width: 360px;
    height: 205px;
  }

  h1 {
    font-size: 16px;
    line-height: 28px;
  }

  .banner-one-discription > .btn-green::before {
    left: -5px;
  }

  h2 {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 30px;
  }
  .outlook-card {
    width: 100%;
    text-align: center;
  }
  .border-box {
    border-width: 5px;
    width: 100%;
    padding: 15px;
  }
  .autobonus .border-box > p {
    margin-bottom: 15px;
    font-size: 16px;
  }
  .btn-play {
    width: 50px;
    height: 50px;
    background-size: 25%;
  }
  .autobonus .btn-play::after {
    width: 20px;
    height: 35px;
    right: -15px;
    top: 15px;
  }
  .stats h2 {
    margin-bottom: 20px;
  }
  .stats-list {
    width: 100%;
  }
  .stats-list > li > span {
    font-size: 38px;
    line-height: 44px;
  }
  .stats-list > li > p {
    line-height: 20px;
  }
  .stats-list > li:before {
    width: 30px;
    height: 30px;
    left: -15px;
    top: 25px;
  }
  .stats .border-box::after {
    width: 60px;
    height: 60px;
  }
  .banner-two h2::after {
    right: 20px;
    width: 40px;
    top: 10px;
    height: 50px;
  }
  .banner-two p {
    font-size: 16px;
  }
  .product h2 {
    margin-bottom: 20px;
  }
  .product p {
    font-size: 12px;
    margin: 0 0 10px;
  }
  .product-card {
    width: 90%;
  }
  .benefit h2 {
    margin-bottom: 20px;
  }
  .benefit-list-images {
    margin-right: 25px;
  }
  .benefit-list p {
    margin: 0;
    color: #fff;
    font-size: 12px;
    & > br {
      display: none;
    }
  }
  .ready h2 {
    margin-bottom: 20px;
  }
  .ready-list > span {
    width: 30px;
    height: 30px;
    line-height: 30px;
    font-size: 30px;
    margin-right: 35px;
  }
  .ready-list > span:before {
    width: 45px;
    height: 45px;
    transform: translate(-10px, -10px);
  }
  .ready-list > span:after {
    width: 68px;
    height: 68px;
    transform: translate(-21px, -50px);
  }
  .ready .border-box {
    margin: auto;
    padding: 15px;
  }
  .ready-list > span > b {
    transform: translateX(20px);
    top: -4px;
  }
  .ready-list > p {
    margin-top: 10px;
    font-size: 12px;
  }
  .form {
    border-width: 5px;
  }
  h3 {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .row {
    flex-wrap: wrap;
  }
  .form-input-box {
    width: 100%;
    margin-bottom: 20px;
  }
  .form p {
    font-size: 9px;
  }
  .form .btn-green {
    margin-bottom: 25px;
  }
  .form p:before {
    width: 25px;
    height: 40px;
    left: -40px;
    top: -14px;
  }
}
