.benefit {
  padding: 100px 0;
  background: url('./../img/benefit-bg.jpg') no-repeat center;
  background-size: cover;
  background-attachment: fixed;
  & > .container {
    position: relative;
  }
  & h2 {
    color: #fff;
    margin: 0;
    padding: 21px 0 22px;
  }
  &-images {
    position: absolute;
    right: 15px;
    top: 0;
    max-width: 475px;
    width: 100%;
    & > img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &-list {
    display: flex;
    padding: 25px 0 30px;
    &:last-of-type {
      padding-bottom: 0;
    }
    &-images {
      width: 40px;
      height: 40px;
      background: $green;
      margin-right: 40px;
      margin-top: 2px;
      & > img {
        transform: translate(10px, -15px);
      }
    }
    & p {
      margin: 0;
      color: #fff;
    }
  }
}
