@media (max-width: 767px) {
  .video-youtube__content {
    width: 540px;
    height: 300px;
  }

  h1 {
    font-size: 26px;
    line-height: 39px;
  }

  .banner-one-discription > p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .banner-two h2::after {
    right: 30px;
    width: 46px;
    height: 50px;
    top: 20px;
  }

  .btn-green {
    min-width: 190px;
    font-size: 10px;
    padding: 10px 5px;
    border-radius: 20px;
    border-bottom: 3px solid #5b8f3a;
  }

  h2 {
    font-size: 26px;
    line-height: 38px;
    margin-bottom: 40px;
  }

  body {
    font-size: 14px;
  }

  .autobonus .border-box > p {
    margin-bottom: 25px;
  }

  .banner-two p {
    font-size: 18px;
  }
  .product p {
    font-size: 18px;
    margin: 0 0 25px;
  }
  .product-card-logo {
    margin: 10px auto;
    height: 60px;
    display: flex;
  }
  .product-card-images {
    height: 90px;
  }
  .form {
    padding: 15px;
  }
}
