.stats {
  padding: 100px 0;
  background: url("./../img/banner-one-bg.jpg") no-repeat center;
  background-attachment: fixed;
  overflow: hidden;
  & > .container {
    position: relative;
  }
  & h2 {
    margin-bottom: 0;
    color: #fff;
    margin-left: auto;
    margin-right: 0;
    max-width: 465px;
    padding: 22px 0 22px 50px;
  }
  .border-box {
    margin-left: auto;
    margin-right: 0;
    position: relative;
    &::after {
      content: '';
      display: block;
      width: 210px;
      height: 210px;
      background: url('./../img/util.png') no-repeat center;
      background-size: contain;
      position: absolute;
      right: 0;
      bottom: 0;
      transform: translate(50%, 50%);
    }
  }
  &-images {
    width: 100%;
    max-width: 675px;
    position: absolute;
    top: 0;
    left: 15px;
    & > img {
      width: 100%;
      height: auto;
    }
  }
  &-list {
    width: 325px;
    margin-left: auto;
    color: #fff;
    & > li {
      margin-bottom: 35px;
      position: relative;
      z-index: 1;
      &:before {
        content: '';
        display: block;
        width: 40px;
        height: 40px;
        position: absolute;
        background: $green;
        z-index: -1;
        left: -20px;
        top: 30px;
      }
      &:last-of-type {
        margin-bottom: 0;
      }
      & > span {
        font-size: 50px;
        line-height: 55px;
        font-weight: 800;
        font-family: 'Playfair Display';
        & > small {
          font-size: 20px;
          line-height: 0;
        }
      }
      & > p {
        margin: 0;
        line-height: 26px;
        font-weight: 500;
      }
    }
  }
}
