.outlook {
  background: #fff;
  padding: 100px 0;
  & h2 {
    text-align: center;
  }
  &-content {
    display: flex;
    justify-content: space-between;
  }
  &-card {
    width: 25%;
    padding-right: 10px;
    &:nth-of-type(odd) img {
      transform: translate(15px, -10px);
    }
    &:nth-of-type(even) img {
      transform: translate(5px, -7.5px);
    }
    &-images {
      height: 70px;
      width: auto;
      background-color: $green;
      margin-right: auto;
      margin-left: 0;
      display: inline-flex;
      margin-bottom: 20px;
      & > img {
        height: 100%;
        width: auto;
      }
    }
    & > p {
      margin: 0;
      font-weight: 500;
    }
  }
}
