.product {
  padding: 100px 0 85px;
  background: #f7f6f4;
  & h2 {
    margin-bottom: 35px;
  }
  & p {
    font-size: 24px;
    font-weight: 500;
    margin: 0 0 45px;
  }
  .row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -15px;
    margin-right: -15px;
  }
  &-card {
    width: 33.3334%;
    padding: 15px;
    &-wrapper {
      border: 5px solid $green;
      height: 100%;
      text-align: center;
      transition: .5s;
      background: #fff;
      display: flex;
      flex-direction: column;
      &:hover {
        box-shadow: 0 0 5px 1px $green;
        transform: scale(1.05);
      }
      & > p {
        padding: 0px 15px;
        margin: 0 0 25px;
        font-size: 18px;
        font-weight: 500;
      }
    }
    &-logo {
      margin: 25px auto;
      height: 90px;
      display: flex;
      & > img {
        margin: auto;
        max-height: 100%;
        max-width: 90%;
        width: auto;
        height: auto;
      }
    }
    &-images {
      height: 125px;
      display: flex;
      overflow: hidden;
      margin-top: auto;
      & > img {
        margin: auto auto 0;
        height: 100%;
        width: auto;
      }
    }
  }
}
