$green: #74b64a;

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

body, html {
  margin: 0;
  padding: 0;
}

body {
  font-size: 16px;
  line-height: 1.5;
  font-family: 'Montserrat';
  color: #000;
}

* {
  box-sizing: border-box;
}

main {
  padding-top: 80px;
}

mark {
  background-color: transparent;
  color: $green;
}

h1 {
  font-size: 50px;
  line-height: 66px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 50px;
  color: #fff;
  font-family: 'Playfair Display';
}

h2 {
  font-size: 40px;
  line-height: 66px;
  margin-bottom: 80px;
  margin-top: 0;
  font-family: 'Playfair Display';
}

h3 {
  margin-top: 0;
  margin-bottom: 40px;
  font-size: 28px;
  font-family: 'Playfair Display';
}

.btn-green {
  min-width: 250px;
  font-size: 12px;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
  padding: 20px 15px 19px;
  border-radius: 40px;
  border: none;
  background: $green;
  border-bottom: 5px solid #5b8f3a;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 20px 1px $green;
  }
}

.video-youtube__content {
  width: 985px;
  height: 590px;
}

.video-youtube_overlay {
  margin: auto;
}

.wrapper {
  margin-bottom: 50px;
}

.btn-play {
  width: 100px;
  height: 100px;
  border: none;
  border-radius: 50%;
  background-color: $green;
  background-image: url('./../img/play.png');
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 15px 1px $green;
  }
}

.border-box {
  width: 100%;
  max-width: 945px;
  border: 15px solid $green;
  padding: 50px 55px;
}

// кнопка меню
.visuallyHidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px; width: 1px;
  margin: -1px; padding: 0; border: 0;
}

.hamburger-item {
  display: none;
}

.hamburger {
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
  width: 30px;
  height: 30px;
  position: relative;
}

.hamburger .bar {
  padding: 0;
  width: 30px;
  height: 4px;
  background-color: $green;
  display: block;
  border-radius: 4px;
  transition: all 0.4s ease-in-out;
  position: absolute;
}

.bar1 {
  top: 0;
}

.bar2,
.bar3 {
  top: 13.5px;
}

.bar3 {
  right: 0;
}

.bar4 {
  bottom: 0;
}

.hamburger3 .bar1 {
  transform-origin: 5%;
}

.hamburger3 .bar4 {
  transform-origin: 5%;
}

.checkbox3:checked + label > .hamburger3 > .bar1{
  transform: rotate(45deg);
  height: 3px;
  width: 42px;
}

.checkbox3:checked + label > .hamburger3 > .bar3{
  transform: rotate(45deg);
  height: 3px;
  background-color: transparent;
}

.checkbox3:checked + label > .hamburger3 > .bar2{
  transform: rotate(-45deg);
  height: 3px;
  background-color: transparent;
}

.checkbox3:checked + label > .hamburger3 > .bar4{
  transform: rotate(-45deg);
  height: 3px;
  width: 42px;
}
