.ready{
  padding: 100px 0;
  & > .container {
    position: relative;
  }
  & h2 {
    margin-bottom: 0;
    padding: 20px 0 20px 555px;
  }
  &-images {
    position: absolute;
    top: 0;
    left: 15px;
    max-width: 490px;
    width: 100%;
    & > img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
  .border-box {
    margin-left: auto;
    margin-right: 0;
    padding: 30px 0 30px 80px;
  }
  &-list {
    margin-left: 290px;
    display: flex;
    padding: 40px 0;
    & > span {
      width: 45px;
      height: 45px;
      background: $green;
      position: relative;
      line-height: 45px;
      font-size: 45px;
      margin-right: 45px;
      &:before {
        content: '';
        display: block;
        width: 65px;
        height: 65px;
        border: 2px solid $green;
        position: absolute;
        transform: translate(-12px, -12px);
      }
      &:after {
        content: '';
        display: block;
        width: 88px;
        height: 88px;
        border: 1px solid $green;
        position: absolute;
        transform: translate(-23px, -68px);
        z-index: 1;
      }
      & > b {
        font-weight: 800;
        transform: translateX(29px);
        display: block;
        position: relative;
        z-index: 2;
        top: -7px;
        font-family: 'Playfair Display';
      }
    }
    & > p {
      margin-top: 17.5px;
      margin-bottom: 0;
      font-weight: 500;}
    }
  }
