@media (max-width: 991px) {
  .hamburger-item {
    display: block;
  }
  .navbar > ul {
    display: none;
    flex-direction: column;
    text-align: center;
    position: absolute;
    top: 50px;
    background: #fff;
    height: auto;
    &.active {
      display: flex;
    }
    & > li {
      padding: 10px 15px;
    }
  }
  main {
    padding-top: 50px;
  }
  .banner-one, .outlook, .autobonus, .stats, .banner-two, .benefit, .ready, .application {
    padding: 50px 0;
  }
  h1 {
    font-size: 32px;
    line-height: 48px;
    margin-bottom: 25px;
  }
  h2 {
    font-size: 32px;
    line-height: 48px;
    margin-bottom: 40px;
  }
  .video-youtube__content {
    width: 650px;
    height: 365px;
  }
  .wrapper {
    margin-bottom: 25px;
  }
  .outlook-content {
    flex-wrap: wrap;
  }
  .outlook-card {
    width: 50%;
    margin-bottom: 30px;
  }
  .autobonus-images, .stats-images, .benefit-images, .ready-images, .application-images {
    display: none;
  }
  .autobonus h2 {
    max-width: 100%;
    padding: 0;
    text-align: center;
  }
  .border-box {
    max-width: 90%;
    margin: auto;
    padding: 25px;
  }

  .stats h2 {
    text-align: center;
    padding: 0;
    margin: 0 auto 40px;
  }

  .stats .border-box {
    margin: auto;
  }

  .stats-list {
    margin: auto;
  }
  .stats-list > li {
    margin-bottom: 20px;
  }
  .stats .border-box::after {
    width: 150px;
    height: 150px;
  }
  .banner-two h2::after {
    right: 75px;
    width: 80px;
    height: 90px;
  }
  .banner-two p {
    margin-left: 25%;
  }
  .product {
    padding: 50px 0 35px;
  }
  .product-card {
    width: 50%;
  }
  .benefit h2 {
    margin: 0 0 40px;
    padding: 0;
    text-align: center;
  }
  .benefit-list {
    padding: 15px 0 20px;
  }
  .ready h2 {
    padding: 0;
    margin-bottom: 40px;
    text-align: center;
  }
  .ready .border-box {
    margin: auto;
    padding: 25px;
  }
  .ready-list {
    margin-left: 15px;
    padding: 20px 0;
  }
  .form {
    margin: auto;
  }
}
