.header {
  position: fixed;
  background: #fff;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  box-shadow: 0 1px 3px 1px #ddd;
}

.navbar {
  width: 100%;
  margin: auto;
  max-width: 1000px;
  & > ul {
    display: flex;
    justify-content: space-around;
    width: 100%;
    & > li {
      text-transform: uppercase;
      padding: 28px 15px;
      & > a {
        position: relative;
        font-size: 14px;
        padding: 7px 0 8px;
        border-bottom: 1px solid transparent;
        border-top: 1px solid transparent;
        font-weight: 600;
        color: #171717;
        transition: all .3s;
        &:hover {
          color: $green;
        }
        &.active {
          border-color: $green;
          color: $green;
          &::before {
            content: '';
            display: block;
            width: 2px;
            height: 2px;
            background: green;
            position: absolute;
            top: 14.5px;
            left: -8px;
          }
          &::after {
            content: '';
            display: block;
            width: 2px;
            height: 2px;
            background: green;
            position: absolute;
            top: 14.5px;
            right: -8px;
          }
        }
      }
    }
  }
}
