.autobonus {
  padding: 100px 0;
  background: #f7f6f4;
  & > .container {
    position: relative;
  }
  & h2 {
    max-width: 650px;
    padding: 44px 0;
    background: #f7f6f4;
    position: relative;
    z-index: 3;
  }
  &-images {
    position: absolute;
    top: 0;
    right: 15px;
    z-index: 2;
    max-width: 650px;
    height: auto;
    & > img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  & .border-box {
    position: relative;
    z-index: 1;
    & > p {
      font-size: 24px;
      font-weight: 500;
      margin-top: 0;
      margin-bottom: 50px;
    }
  }
  & .btn-play {
    position: relative;
    &::after {
      content: '';
      background: url('./../img/btn-line2.png') no-repeat;
      background-size: contain;
      display: block;
      width: 40px;
      height: 70px;
      position: absolute;
      right: -50px;
      top: 5px;
      transform: translate(100%, -50%);
    }
  }
}
