.banner-one {
  background: url('./../img/banner-one-bg.jpg') no-repeat center;
  background-size: cover;
  padding: 100px 0;
  &-discription {
    max-width: 780px;
    width: 100%;
    margin: auto;
    & > p {
      font-size: 24px;
      color: #fff;
      font-weight: 500;
      margin-bottom: 30px;
      margin-top: 0;
    }
    & > .btn-green {
      margin-left: auto;
      margin-right: 0;
      display: block;
      position: relative;
      &::before {
        content: '';
        background: url('./../img/btn-line.png') no-repeat;
        background-size: contain;
        display: block;
        width: 70px;
        height: 40px;
        position: absolute;
        left: -50px;
        top: 5px;
        transform: translate(-100%, -50%);
      }
    }
  }
}
