.application {
  padding: 100px 0;
  background: #f7f6f4;
  & > .container {
    position: relative;
  }
  &-images {
    position: absolute;
    top: 75px;
    right: 15px;
    z-index: 1;
    max-width: 645px;
    width: 100%;
    & > img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
}

.form {
  width: 100%;
  max-width: 620px;
  border: 15px solid $green;
  padding: 45px 70px 45px 35px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;
  background: #f7f6f4;
  .row {
    display: flex;
    margin-left: -15px;
    margin-right: -15px;
  }
  &-input-box {
    padding: 0 15px;
    width: 50%;
    display: flex;
    flex-direction: column;
    margin-bottom: 35px;
    & > label {
      font-size: 12px;
      line-height: 24px;
    }
    & > input {
      font-size: 14px;
      border-top: none;
      border-left: none;
      border-right: none;
      background: transparent;
      padding: 5px 0;
      line-height: 24px;
      font-weight: 500;
    }
  }
  & .btn-green {
    margin-bottom: 45px;
    margin-right: auto;
  }
  & p {
    margin-top: 0;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: 0;
    display: inline-block;
    font-size: 10px;
    font-weight: 500;
    position: relative;
    &:before {
      content: '';
      display: block;
      width: 40px;
      height: 55px;
      position: absolute;
      background: url('./../img/form-btn-line.png') no-repeat center;
      background-size: contain;
      left: -70px;
      top: -32px;
    }
  }
}
